/* eslint-disable no-unused-vars */
/* eslint-disable no-undef */
/* eslint-disable @typescript-eslint/consistent-type-imports */
import * as am4core from '@amcharts/amcharts4/core';
import { formatDate } from '@angular/common';
import type { OnDestroy, OnInit } from '@angular/core';
import { Component, ViewEncapsulation } from '@angular/core';
import { BehaviorSubject, interval, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { config } from 'src/app/shared/app.config';
import { SessionService } from 'src/app/shared/services/session-service/session.service';
import { IStatus } from '../interfaces/orders.interface';
import { OrdersService } from '../services/orders.service';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class OrdersComponent implements OnInit, OnDestroy {
  public display = false;
  public startDate = {
    display: formatDate(new Date(), 'd MMM yyyy', this.session.locale),
    value: new Date(),
  };
  public endDate = {
    display: formatDate(new Date(), 'd MMM yyyy', this.session.locale),
    value: new Date(),
  };
  public refreshList = false;
  public refresh$ = new BehaviorSubject<boolean>(false);
  public isLoading = true;
  public chartData: { value: number; category: string; color: any }[] = [];
  public totalOrders = 0;
  private _destroy$ = new Subject<any>();

  constructor(private apiService: OrdersService, private session: SessionService) {}

  ngOnDestroy() {
    this._destroy$.next(true);
  }

  ngOnInit(): void {
    this.getSummary();
    interval(parseInt(config.requestRate))
      .pipe(takeUntil(this._destroy$))
      .subscribe(() => {
        this.refreshList = !this.refreshList;
        this.refresh$.next(true);
        this.getSummary();
      });
  }

  getSummary() {
    this.isLoading = true;
    const startDate = formatDate(this.startDate.value, 'shortDate', this.session.locale),
      endDate = formatDate(this.endDate.value, 'shortDate', this.session.locale);
    this.apiService.getSummary(startDate, endDate).subscribe((res) => {
      if (res.Result == 'OK') {
        this.totalOrders = res.TotalOrders;
        this.chartData = res.Summary.filter((e) => e.Status !== IStatus.cancelled).map(({ Status, Count }) => ({
          value: Count,
          category: Status,
          color: this.getChartColor(Status),
        }));
      }
      this.isLoading = false;
    });
  }

  getChartColor(status: any) {
    switch (status) {
      case IStatus.placed: {
        return am4core.color('#ffeb3b');
      }
      case IStatus.inProgress: {
        return am4core.color('#03a9f4');
      }
      case IStatus.hasError: {
        return am4core.color('#cd363e');
      }
      case IStatus.delivered: {
        return am4core.color('#46cd36');
      }
      default: {
        return am4core.color('#4b4c4a');
      }
    }
  }

  submitDate(event: any) {
    const formattedDate = formatDate(this.endDate.value, 'd MMM yyyy', this.session.locale);
    this.endDate.display = formattedDate;
    this.startDate.display = formatDate(this.startDate.value, 'd MMM yyyy', this.session.locale);
    event?.toggle();
    this.refreshList = !this.refreshList;
    this.refresh$.next(true);
    this.getSummary();
  }

  displayCreateOrder() {
    // Hide Full page scroll
    const mainPage = document.getElementsByClassName('page-wrap')[0] as HTMLElement;
    mainPage.style.overflowY = 'hidden';
    mainPage.scroll({
      behavior: 'smooth',
      top: 0,
      left: 0,
    });
    // create menu variables
    const SliderMainButton = document.getElementsByClassName('right-pollSlider-button')[0];
    //  event.preventDefault();
    if (SliderMainButton.classList.contains('box_rotate')) {
      SliderMainButton.classList.remove('box_rotate');
      SliderMainButton.children[0].innerHTML = `<i class="fas fa-plus"></i> [[[[New Order]]]]`;
    } else {
      SliderMainButton.classList.add('box_rotate');
      SliderMainButton.children[0].innerHTML = `<i class="fas fa-times"></i> [[[[Close order]]]]`;
    }

    this.display = !this.display;
    if (!this.display) {
      const mainPage = document.getElementsByClassName('page-wrap')[0] as HTMLElement;
      mainPage.style.overflowY = 'auto';
    }
  }

  orderCreated(_: boolean) {
    this.getSummary();
  }

  onClosed(_: boolean) {
    const mainPage = document.getElementsByClassName('page-wrap')[0] as HTMLElement;

    mainPage.style.overflowY = 'auto';

    // animation on close button
    const SliderMainButton = document.getElementsByClassName('right-pollSlider-button')[0];
    SliderMainButton?.classList?.remove('box_rotate');
    SliderMainButton.children[0].innerHTML = `<i class="fas fa-plus"></i> [[[[New Order]]]]`;
    this.display = false;
  }
}
