/* eslint-disable no-undef */
/* eslint-disable @typescript-eslint/consistent-type-imports */
import type { AfterViewInit, OnDestroy, OnInit } from '@angular/core';
import { Component, Input, ViewEncapsulation } from '@angular/core';
import type { MultiSelectComponent } from '@progress/kendo-angular-dropdowns';
import type { FilterService, GridComponent, GridDataResult } from '@progress/kendo-angular-grid';
import type { CompositeFilterDescriptor, State } from '@progress/kendo-data-query';
import { BehaviorSubject, fromEvent, Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
import { HumanizerService } from 'src/app/shared/services/humanizer/humanizer.service';
import { OrdersService } from '../../../services/orders.service';
import { IOrderStatus } from './../../../interfaces/orders.interface';

@Component({
  selector: 'app-list-view',
  templateUrl: './list-view.component.html',
  styleUrls: ['./list-view.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ListViewComponent implements OnInit, OnDestroy, AfterViewInit {
  @Input() startDate: any;
  @Input() endDate: any;
  @Input() refreshList = false;
  @Input() refresh = new BehaviorSubject(false);
  public gridData: any[] = [];
  public gridView!: GridDataResult;
  public isLoading = true;
  public ngSelectOrderStatus: any = [''];
  public filter!: CompositeFilterDescriptor;

  public orderStatuses = [
    {
      OrderStatus: '',
      value: '',
    },
  ];
  public gridState: State | any = {
    sort: [],
    skip: 0,
    take: 100,
  };

  public totalCount = 0;
  public actions = [
    {
      name: '[[[[Details]]]]',
      label: '[[[[Details]]]]',
      class: 'btn btn-sm btn-outline-secondary',
      icon: 'fas fa-list',
      action: (rowData: any) => this.showDetails(rowData),
    },
  ];
  private gridRequest: {
    pageNumber?: number;
    pageSize?: number;
    mobileNumber?: string;
    orderStatus?: IOrderStatus[];
    orderNumber?: string;
    customerName?: string;
    createdBy?: string;
    price?: string;
  } = {};
  private stateChange = new BehaviorSubject<any>(this.gridState);
  private defaultOrder = 'LastStatusUpdatedDate-asc';
  destroyed = new Subject<boolean>();

  constructor(private apiService: OrdersService, private humanizerService: HumanizerService) {
    this.orderStatuses = Object.keys(IOrderStatus)
      .filter((x) => !(parseInt(x) >= 0))
      .map((e) => ({
        OrderStatus: this.humanizerService.transform(e),
        value: IOrderStatus[e],
      }));
  }

  ngOnInit(): void {
    this.refresh
      .pipe(
        takeUntil(this.destroyed),
        filter((e) => !!e)
      )
      .subscribe(() => this.stateChange.next(this.gridState));

    this.stateChange
      .pipe(
        takeUntil(this.destroyed),
        filter((e) => !!e)
      )
      .subscribe(() => {
        this.getDataGridValues();
      });
  }

  ngOnDestroy() {
    this.destroyed.next(true);
  }

  ngAfterViewInit() {
    this.propgateOrderStatusChanges();
  }

  propgateOrderStatusChanges = () => {
    const statusFilter = document.querySelector(
      'kendo-grid > div > div > div > table > thead > tr:nth-child(1) > th:nth-child(3) > span > kendo-grid-filter-menu > a'
    ) as HTMLAnchorElement;
    fromEvent(statusFilter, 'click')
      .pipe(takeUntil(this.destroyed))
      .subscribe(() => {
        const clearButton = document.querySelector(
          'kendo-grid-filter-menu-container > form > div > div > button:nth-child(1)'
        ) as HTMLButtonElement;
        fromEvent(clearButton, 'click')
          .pipe(takeUntil(this.destroyed))
          .subscribe((event) => {
            event.preventDefault();
            this.ngSelectOrderStatus = [];
            this.gridRequest.orderStatus = undefined;
            this.gridState.filter.filters = this.gridState.filter?.filters.filter(
              (e: any) => e.filters[0].field != 'OrderStatus'
            );
            this.stateChange.next(this.gridState);
          });
      });
  };

  showDetails(rowData: any) {
    console.log(rowData);
  }

  public onPageChange(state: any): void {
    if (this.gridState.skip != state.skip) {
      this.gridState = state;
      this.stateChange.next(state);
    }
  }

  dataStateChanged(state: State) {
    this.gridState = state;

    // Handle Kendo Filters
    const numberFilter = this.gridState.filter?.filters.find((e: any) => e.filters[0].field == 'Number')?.filters[0]
      ?.value;
    this.gridRequest.orderNumber = numberFilter;

    const customerNameFilter = this.gridState.filter?.filters.find((e: any) => e.filters[0].field == 'Customer.Name')
      ?.filters[0]?.value;
    this.gridRequest.customerName = customerNameFilter;

    const createdByFilter = this.gridState.filter?.filters.find((e: any) => e.filters[0].field == 'CreatedBy')
      ?.filters[0]?.value;
    this.gridRequest.createdBy = createdByFilter;

    const mobileNoFilter = this.gridState.filter?.filters.find((e: any) => e.filters[0].field == 'Customer.MobileNo')
      ?.filters[0]?.value;
    this.gridRequest.mobileNumber = mobileNoFilter;

    const orderStatusFilter = this.gridState.filter?.filters.find((e: any) => e.filters[0].field == 'OrderStatus')
      ?.filters[0]?.value;
    this.gridRequest.orderStatus = orderStatusFilter;

    // Handle Kendo Sort
    if (this.gridState.sort && this.gridState.sort[0] && this.gridState.sort[0].dir) {
      if (this.gridState.sort[0].field == 'Price') {
        this.defaultOrder = `PriceAmount-${this.gridState.sort[0].dir}`;
      } else {
        this.defaultOrder = `${this.gridState.sort[0].field}-${this.gridState.sort[0].dir}`;
      }
    } else {
      this.defaultOrder = 'LastStatusUpdatedDate-asc';
    }

    this.stateChange.next(state);
  }

  private getDataGridValues() {
    this.isLoading = true;
    const requestBody = this.gridRequest;
    const orderRequest = {
      pageNumber: Math.ceil(this.gridState.skip / this.gridState.take + 1),
      from: this.startDate,
      to: this.endDate,
      pageSize: 100,
      orderBy: this.defaultOrder,
      statuses: requestBody.orderStatus ?? undefined,
      orderNumber: requestBody.orderNumber ?? '',
      customerName: requestBody.customerName ?? '',
      createdBy: requestBody.createdBy ?? '',
      mobileNumber: requestBody.mobileNumber ?? '',
    };
    if (this.startDate && this.endDate) {
      this.apiService.getOrders(orderRequest).subscribe((res) => {
        if (res.Result == 'OK') {
          this.gridData = res.Orders;
          this.totalCount = res.TotalItemsCount;
        }
        this.isLoading = false;
      });
    } else {
      this.isLoading = false;
    }
  }

  public orderStatusFilter(values: any, filterService: FilterService): void {
    this.ngSelectOrderStatus = values;
    filterService.filter({
      filters: values.map((value: any) => ({
        field: 'OrderStatus',
        operator: 'eq',
        value: value,
      })),
      logic: 'and',
    });
  }

  public exportToExcel(grid: GridComponent): void {
    grid.saveAsExcel();
  }

  public exportToPdf(grid: GridComponent): void {
    grid.saveAsPDF();
  }

  gridAction(gridActionHandler: any, rowData: any) {
    // pass The Row data along with the action provided from actions list
    gridActionHandler(rowData);
  }

  public handleToggleClick(multiselect: MultiSelectComponent): void {
    multiselect.focus();
    multiselect.toggle();
  }
}
