// eslint-disable-next-line @typescript-eslint/consistent-type-imports
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import type {
  IAddress,
  ICreateAddress,
  ICreateCustomer,
  ICreateOrder,
  ICustomer,
  IOrder,
  IOrderStatus,
  IProducts,
} from '../interfaces/orders.interface';

@Injectable({
  providedIn: 'root',
})
export class OrdersService {
  private readonly apiEndPoint = environment.APIUrl + 'OrderShipmentScheduling/Orders/';

  constructor(private http: HttpClient) {}

  getOrders(orderRequest: {
    pageNumber: number;
    pageSize: number;
    from: string;
    to: string;
    orderBy: string;
    orderNumber?: string;
    statuses?: IOrderStatus[];
    customerName?: string;
    createdBy?: string;
    mobileNumber?: string;
  }) {
    return this.http.get<{
      Result: string;
      TotalItemsCount: number;
      Orders: IOrder[];
    }>(this.apiEndPoint + `Get`, {
      params: {
        // ...orderRequest
        pageNumber: orderRequest.pageNumber,
        pageSize: orderRequest.pageSize,
        from: orderRequest.from,
        to: orderRequest.to,
        orderBy: orderRequest.orderBy,
        orderNumber: orderRequest.orderNumber ?? '',
        statuses: orderRequest.statuses ?? [],
        customerName: orderRequest.customerName ?? '',
        createdBy: orderRequest.createdBy ?? '',
        mobileNumber: orderRequest.mobileNumber ?? '',
      },
    });
  }

  createOrder(order: ICreateOrder) {
    return this.http.post<{ Result: string; Errors?: string[] }>(this.apiEndPoint + `Create`, order);
  }

  getSummary(from: string, to: string) {
    return this.http.get<{
      Result: string;
      TotalOrders: number;
      Summary: {
        Status: string;
        Count: number;
      }[];
    }>(this.apiEndPoint + `Summary?from=${from}&to=${to}`);
  }

  getCustomer(customerName: string) {
    return this.http.get<{
      Result: string;
      Customers: {
        Id: number;
        Name: string;
        MobileNo: string;
        Addresses: {
          Id: number;
          Address: string;
          City: string;
        }[];
      }[];
    }>(this.apiEndPoint + `GetCustomers?search=${customerName}`);
  }

  createCustomer(customer: ICreateCustomer) {
    return this.http.post<{
      Result: string;
      Errors: string[];
      Customer: ICustomer;
    }>(this.apiEndPoint + `CreateCustomer`, customer);
  }

  createCustomerAddress(customerId: number, address: ICreateAddress) {
    return this.http.post<{
      Result: string;
      Errors: string[];
      Address: IAddress;
    }>(this.apiEndPoint + `CreateCustomerAddress`, {
      customerId,
      dto: address,
    });
  }

  getProducts() {
    return this.http.get<{ Result: string; Products: IProducts[] }>(this.apiEndPoint + `GetProducts`);
  }
}
