import * as am4charts from '@amcharts/amcharts4/charts';
import * as am4core from '@amcharts/amcharts4/core';
import am4themes_animated from '@amcharts/amcharts4/themes/animated';
import am4themes_dark from '@amcharts/amcharts4/themes/dark';
import type { OnChanges, SimpleChanges } from '@angular/core';
import { Component, Input } from '@angular/core';
import { config } from 'src/app/shared/app.config';

@Component({
  selector: 'app-chart-view',
  templateUrl: './chart-view.component.html',
  styleUrls: ['./chart-view.component.scss'],
})
export class ChartViewComponent implements OnChanges {
  @Input() totalOrders = 0;
  @Input() data = [];
  @Input() refreshList = false;

  private chart!: am4charts.PieChart;
  constructor() {}

  ngOnChanges(_simpleChanges: SimpleChanges) {
    if (_simpleChanges.data && _simpleChanges.data.currentValue) {
      if (this.chart) {
        this.chart.dispose();
      }
      this.createChart();
    }
  }

  createChart() {
    // Themes begin
    am4core.useTheme(am4themes_animated);
    am4core.useTheme(am4themes_dark);
    am4core.addLicense(config.amChartLicense);

    // Create chart
    this.chart = am4core.create('chartdiv', am4charts.PieChart);

    // Add data
    this.chart.data = this.data;

    // Add and configure Series
    const pieSeries = this.chart.series.push(new am4charts.PieSeries());
    pieSeries.dataFields.value = 'value';
    pieSeries.dataFields.category = 'category';
    pieSeries.slices.template.propertyFields.fill = 'color';

    // Let's cut a hole in our Pie chart the size of 40% the radius
    this.chart.innerRadius = am4core.percent(40);

    // Set up fills
    pieSeries.slices.template.fillOpacity = 1;
    // pieSeries.labels.template.bent = true;
    // pieSeries.labels.template.padding(1, 1, 1, 1);
    pieSeries.alignLabels = false;
    pieSeries.ticks.template.events.on('ready', hideSmall);
    pieSeries.ticks.template.events.on('visibilitychanged', hideSmall);
    pieSeries.labels.template.events.on('ready', hideSmall);
    pieSeries.labels.template.events.on('visibilitychanged', hideSmall);

    function hideSmall(ev: any) {
      if (ev.target.dataItem && ev.target.dataItem.values.value.percent < 1) {
        ev.target.hide();
      } else {
        ev.target.show();
      }
    }
    const hoverdState = pieSeries.slices.template.states.getKey('hover');
    if (hoverdState) {
      // hs.properties.scale = 1;
      hoverdState.properties.fillOpacity = 0.5;
    }
    const activeState = pieSeries.slices.template.states.getKey('active');
    if (activeState) {
      activeState.properties.scale = 1;
      activeState.properties.shiftRadius = 0;
      activeState.properties.fillOpacity = 0.5;
    }

    // Add a legend
    this.chart.legend = new am4charts.Legend();
    this.chart.clickable = false;
    pieSeries.clickable = false;

    this.chart.legend.itemContainers.template.clickable = false;
    this.chart.legend.itemContainers.template.focusable = false;
    this.chart.legend.itemContainers.template.cursorOverStyle = am4core.MouseCursorStyle.default;

    // add markers template
    let markerTemplate = this.chart.legend.markers.template;
    markerTemplate.width = 15;
    markerTemplate.height = 8;

    // Responsive
    this.chart.responsive.enabled = true;
    this.chart.responsive.rules.push({
      relevant: (target) => {
        if (target.pixelWidth <= 600) {
          return true;
        }
        return false;
      },
      state: (target, stateId): any => {
        if (target instanceof am4charts.PieSeries) {
          var state = target.states.create(stateId);

          var labelState = target.labels.template.states.create(stateId);
          labelState.properties.disabled = true;

          var tickState = target.ticks.template.states.create(stateId);
          tickState.properties.disabled = true;
          return state;
        }
        return null;
      },
    });

    // eslint-disable-next-line no-undef
    window.addEventListener('resize', () => {
      this.chart.legend.validateLayout();
      this.chart.validateRawData();
      this.chart.invalidateData();
      this.chart.validateData();
    });

    // Play initial series animation
    pieSeries.appear();
  }
}
