/* eslint-disable @typescript-eslint/consistent-type-imports */
import type { OnInit } from '@angular/core';
import { Component, EventEmitter, Output, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Validators } from 'angular-reactive-validation';
import type { ICustomer } from '../../../interfaces/orders.interface';
import { OrdersService } from '../../../services/orders.service';
import { AlertService } from './../../../../../shared/services/alert-service/alert-service.service';
import type { ICreateCustomer } from './../../../interfaces/orders.interface';

@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.component.html',
  styleUrls: ['./add-customer.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AddCustomerComponent implements OnInit {
  public isLoading = false;
  ngCustomerForm!: FormGroup;
  submitted = false;
  @Output() closed = new EventEmitter<boolean>();
  @Output() customerAdded = new EventEmitter<ICustomer>();
  constructor(private fb: FormBuilder, private alertService: AlertService, private apiService: OrdersService) {}

  public get elements() {
    return this.ngCustomerForm.controls;
  }

  public get addressElements() {
    return (this.elements.customerAddress as FormGroup).controls;
  }

  ngOnInit(): void {
    this.createForm();
  }

  createForm() {
    this.ngCustomerForm = this.fb.group({
      firstName: [
        '',
        [
          Validators.required('[[[[First name is required]]]]'),
          Validators.maxLength(100, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`),
        ],
      ],
      lastName: [
        '',
        [
          Validators.required('[[[[Last name is required]]]]'),
          Validators.maxLength(100, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`),
        ],
      ],
      phone: [
        '',
        [
          Validators.required('[[[[Phone number is required]]]]'),
          Validators.pattern(`^([+]?)(?![0| ]+$)[0-9 ]+$`),
          Validators.maxLength(20),
        ],
      ],
      customerAddress: this.fb.group({
        name: [
          '',
          [
            Validators.required('[[[[Name is required]]]]'),
            Validators.maxLength(50, (maxLength) => `Maximum length is ${maxLength}`),
          ],
        ],
        address: [
          '',
          [
            Validators.required('[[[[Address is required]]]]'),
            Validators.maxLength(250, (maxLength) => `Maximum length is ${maxLength}`),
          ],
        ],
        line1: ['', Validators.maxLength(250, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`)],
        line2: [''],
        city: ['', Validators.maxLength(100, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`)],
        zipCode: ['', Validators.maxLength(100, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`)],
        addressPhone: [
          '',
          [
            // Validators.required('Address Phone is required'),
            Validators.pattern(`^([+]?)(?![0| ]+$)[0-9 ]+$`),
            Validators.maxLength(20, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`),
          ],
        ],
        instructions: ['', Validators.maxLength(250, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`)],
      }),
    });
  }

  cancel() {
    this.ngCustomerForm.reset();
    this.closed.emit(true);
  }

  submitNewCustomer(formGroup: FormGroup) {
    this.submitted = true;
    this.isLoading = true;
    if (formGroup.valid) {
      const customer: ICreateCustomer = {
        FirstName: formGroup.value.firstName,
        LastName: formGroup.value.lastName,
        MobileNo: formGroup.value.phone,
        Address: {
          Name: formGroup.value.customerAddress.name,
          Address: formGroup.value.customerAddress.address,
          AddressTwo: formGroup.value.customerAddress.line1,
          City: formGroup.value.customerAddress.city,
          ZipCode: formGroup.value.customerAddress.zipCode,
          PhoneNo: formGroup.value.customerAddress.addressPhone,
          Instructions: formGroup.value.customerAddress.instructions,
        },
      };

      this.apiService.createCustomer(customer).subscribe((res) => {
        if (res.Result == 'OK') {
          this.alertService.showAlert('success', '[[[[Customer Created Succesfully]]]]!');
          this.customerAdded.emit(res.Customer);
          this.cancel();
        }
        this.isLoading = false;
      });
    }
  }
}
