import type { CustomColumn } from 'src/app/shared/components/lynx-grid/interfaces/lynx-grid.interface';

export interface ICustomer {
  Id: number;
  FirstName: string;
  LastName: string;
  MobileNo: string;
  Address: IAddress;
}
/**
 * @validations:
 * @FirstName [Required] [MaxLength(100)]
 * @LastName  [Required] [MaxLength(100)]
 * @MobileNo  [Required] [MaxLength(20)] pattern ["^(\+?)(?![0| ]+$)[\d ]+$"]
 */
export interface ICreateCustomer {
  FirstName: string;
  LastName: string;
  MobileNo: string;
  Address: ICreateAddress;
}

/**
 * @Validations:
 * @Name  [Required] [MaxLength(50)]
 * @Address [Required] [MaxLength(250)]
 * @AddressTwo [MaxLength(250)]
 * @City [MaxLength(100)]
 * @ZipCode [MaxLength(100)]
 * @PhoneNo  [MaxLength(20)] "^(\+?)(?![0| ]+$)[\d ]+$"
 * @Instructions [MaxLength(250)]
 */
export interface ICreateAddress {
  Name: string;
  Address: string;
  AddressTwo: string;
  City: string;
  ZipCode: string;
  PhoneNo: string;
  Instructions: string;
}

export interface IAddress {
  Id: number;
  Address: string;
}

export interface IProducts {
  Id: number;
  Number: string;
  Name: string;
  Description: string;
  Sepecifications: string[];
}

export enum IOrderStatus {
  Placed = 1,
  AwaitingPayment = 2,
  Processing = 3,
  ProcessingAttempted = 4,
  Fulfilled = 5,
  ShipmentConfirmed = 6,
  AwaitingPickup = 7,
  AttemptedPickup = 8,
  OutForDelivery = 9,
  AttemptedDelivery = 10,
  Delivered = 11,
  Cancelled = 12,
  PartiallyDelivered = 13,
}

export interface IOrder {
  Id: number;
  Number: string;
  CreatedDate: CustomColumn | string;
  Status: string;
  Customer: string;
  Price: string;
  CreatedBy: string;
}

/**
 * @Validations:
 * @CustomerId  [Required]
 * @CustomerAddressId [Required]
 * @DeliveryOptions [Required]
 * @Products [Required]
 */
export interface ICreateOrder {
  CustomerId: number;
  CustomerAddressId: number;
  DeliveryOptions: IDeliveryOptions;
  Products: ICreateOrderProduct[];
}

/**
 * @Validations:
 * @DeliverAtOnce  [Required]
 * @CustomerAddressId [Required]
 * @DeliveryOptions [Required]
 * @Products [Required]
 */
export interface IDeliveryOptions {
  DeliverAtOnce: boolean;
  MaxDeliveryTime?: number;

  AfterTime?: string; //[From Time] timespan
  BeforeTime?: string; //[Before Time] timespan

  AvailableDeliveryDate?: string; //datetime
}

export interface ICreateOrderProduct {
  ProductId: number;
  Quantity: number;
}

export enum IStatus {
  placed = 'Placed',
  inProgress = 'In Progress',
  delivered = 'Delivered',
  hasError = 'Has Error',
  cancelled = 'Cancelled',
}
