/* eslint-disable @typescript-eslint/consistent-type-imports */
import type { OnInit } from '@angular/core';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Validators } from 'angular-reactive-validation';
import { AlertService } from 'src/app/shared/services/alert-service/alert-service.service';
import type { ICreateAddress } from '../../../interfaces/orders.interface';
import { OrdersService } from '../../../services/orders.service';
import type { IAddress } from './../../../interfaces/orders.interface';

@Component({
  selector: 'app-add-address',
  templateUrl: './add-address.component.html',
  styleUrls: ['./add-address.component.scss'],
})
export class AddAddressComponent implements OnInit {
  public isLoading = false;
  ngAddressForm!: FormGroup;
  submitted = false;
  @Input() customerId = -1;
  @Output() closed = new EventEmitter<boolean>();
  @Output() addressAdded = new EventEmitter<IAddress>();

  constructor(private fb: FormBuilder, private alertService: AlertService, private apiService: OrdersService) {}

  public get addressElements() {
    return this.ngAddressForm.controls;
  }

  ngOnInit(): void {
    this.createForm();
  }

  createForm() {
    this.ngAddressForm = this.fb.group({
      name: [
        '',
        [
          Validators.required('[[[[Name is required]]]]'),
          Validators.maxLength(50, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`),
        ],
      ],
      address: [
        '',
        [
          Validators.required('[[[[Address is required]]]]'),
          Validators.maxLength(250, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`),
        ],
      ],
      line1: ['', Validators.maxLength(250, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`)],
      line2: [''],
      city: ['', Validators.maxLength(100, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`)],
      zipCode: ['', Validators.maxLength(100, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`)],
      addressPhone: [
        '',
        [
          Validators.pattern(`^([+]?)(?![0| ]+$)[0-9 ]+$`),
          Validators.maxLength(20, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`),
        ],
      ],
      instructions: ['', Validators.maxLength(250, (maxLength) => `[[[[Maximum length is]]]] ${maxLength}`)],
    });
  }

  cancel() {
    this.ngAddressForm.reset();
    this.closed.emit(true);
  }

  submitAddress(formGroup: FormGroup) {
    this.submitted = true;
    this.isLoading = true;
    if (formGroup.valid && this.customerId && this.customerId != -1) {
      const address: ICreateAddress = {
        Name: formGroup.value.name,
        Address: formGroup.value.address,
        AddressTwo: formGroup.value.line1,
        City: formGroup.value.city,
        ZipCode: formGroup.value.zipCode,
        PhoneNo: formGroup.value.addressPhone,
        Instructions: formGroup.value.instructions,
      };
      this.apiService.createCustomerAddress(this.customerId, address).subscribe((res) => {
        if (res.Result == 'OK') {
          this.alertService.showAlert('success', res.Errors.join(','));
          this.addressAdded.emit(res.Address);
          this.cancel();
        }
        this.isLoading = false;
      });
    }
  }
}
