/* eslint-disable @typescript-eslint/consistent-type-imports */
import { formatDate } from '@angular/common';
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, FormGroupDirective } from '@angular/forms';
import { Validators } from 'angular-reactive-validation';
import * as moment from 'moment';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { slideInOutAnimation } from 'src/app/shared/animations/animations';
import { IFormErrors } from 'src/app/shared/directives/forms-directives/validation-error-directive/error-message.interface';
import { SessionService } from 'src/app/shared/services/session-service/session.service';
import { IAddress, ICreateOrder, ICustomer } from '../../../interfaces/orders.interface';
import { OrdersService } from '../../../services/orders.service';
import { AlertService } from './../../../../../shared/services/alert-service/alert-service.service';
@Component({
  selector: 'app-create-order',
  templateUrl: './create-order.component.html',
  styleUrls: ['./create-order.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: slideInOutAnimation,
})
export class CreateOrderComponent implements OnInit, OnDestroy {
  @ViewChild(FormGroupDirective) myForm: any;
  public today = new Date();
  public isLoading = false;
  @Input() display = false;
  addCustomerDisplay = false;
  addAddressDisplay = false;
  @Output() orderClosed = new EventEmitter<boolean>();
  @Output() orderCreated = new EventEmitter<any>();
  errorMessages: IFormErrors[] = [];
  ngOrderForm!: FormGroup;
  submitted = false;
  customerList: {
    id: number;
    name: string;
    addresses: { Id: number; Address: string }[];
  }[] = [];
  customerName = '';
  productsList: { id: number; name: string }[] = [];
  shippingAddresses: { id: number; name: string }[] = [];
  destroyed = new Subject<boolean>();
  deliveryWithin: any = [];
  is12HTimeZone = false;
  timeValidationError = false;
  constructor(
    private fb: FormBuilder,
    private alertService: AlertService,
    private apiService: OrdersService,
    private sessionService: SessionService
  ) {
    this.is12HTimeZone = this.checkTimeFormat(this.sessionService.locale);
    // create Array of numbers from 1 to 12
    this.deliveryWithin = Array.from({ length: 12 }, (_, i) => i + 1).map((e) => ({ id: e }));
  }

  checkTimeFormat(language: string) {
    const options = new Intl.DateTimeFormat(language, {
      hour: 'numeric',
    }).resolvedOptions();
    return options.hour12 ?? false;
  }

  ngOnDestroy() {
    this.destroyed.next(true);
  }

  filterCustomers(event: any) {
    let query = event.query;
    this.customerName = query;
    this.apiService
      .getCustomer(query.toLowerCase())
      .pipe(takeUntil(this.destroyed))
      .subscribe((res) => {
        if (res.Result == 'OK') {
          this.customerList = res.Customers.map(({ Id, Name, Addresses }) => ({
            id: Id,
            name: Name,
            addresses: Addresses,
          }));
        }
      });
  }

  getProducts() {
    this.apiService
      .getProducts()
      .pipe(takeUntil(this.destroyed))
      .subscribe((res) => {
        if (res.Result == 'OK') {
          this.productsList = res.Products.map((res) => ({
            id: res.Id,
            name: `${res.Number}: ${res.Name} ${
              res.Sepecifications.length ? ' (' + res.Sepecifications.join(',') + ')' : ''
            }`,
          }));
        }
      });
  }

  ngOnInit(): void {
    this.createForm();
    this.addProduct();
    this.getProducts();

    if (this.elements) {
      const deliveryOptionsForm = this.elements.deliveryOptions as FormGroup;
      deliveryOptionsForm.get('withinHours')?.disable();
      deliveryOptionsForm.get('betweenFrom')?.disable();
      this.elements.scheduledDelivery.disable();
      deliveryOptionsForm.get('betweenTo')?.disable();
      deliveryOptionsForm
        .get('deliveryOption')
        ?.valueChanges.pipe(takeUntil(this.destroyed))
        .subscribe((e) => {
          if (e) {
            switch (e) {
              case 'deliverAnyTime': {
                deliveryOptionsForm.get('betweenFrom')?.disable();
                deliveryOptionsForm.get('betweenTo')?.reset();
                deliveryOptionsForm.get('betweenFrom')?.reset();
                deliveryOptionsForm.get('withinHours')?.disable();
                deliveryOptionsForm.get('betweenTo')?.disable();

                this.elements.scheduledDelivery.enable();
                this.timeValidationError = false;
                break;
              }
              case 'within': {
                deliveryOptionsForm.get('withinHours')?.enable();
                deliveryOptionsForm.get('betweenTo')?.reset();
                deliveryOptionsForm.get('betweenFrom')?.reset();
                deliveryOptionsForm.get('betweenFrom')?.disable();
                deliveryOptionsForm.get('betweenTo')?.disable();
                this.elements.scheduledDelivery.disable();
                this.timeValidationError = false;
                break;
              }
              case 'between': {
                deliveryOptionsForm.get('betweenTo')?.enable();
                deliveryOptionsForm.get('betweenFrom')?.enable();
                deliveryOptionsForm.get('withinHours')?.disable();
                this.elements.scheduledDelivery.enable();
                break;
              }
            }
          }
        });

      this.elements.customerId.valueChanges.pipe(takeUntil(this.destroyed)).subscribe((value: any) => {
        if (value && value.id) {
          this.shippingAddresses =
            this.customerList
              .find((e) => e.id == parseInt(value.id))
              ?.addresses.map((e) => ({ id: e.Id, name: e.Address })) ?? [];
          this.elements.shippingAddressId.setValue(this.shippingAddresses[0]?.id);
        }
      });
      this.timeValidation();
    }
  }

  public timeValidation() {
    this.deliveryElements.betweenTo.valueChanges.pipe(takeUntil(this.destroyed)).subscribe((value: any) => {
      if (this.deliveryElements.betweenFrom.value && this.deliveryElements.betweenFrom.value > value) {
        this.timeValidationError = true;
      } else {
        this.timeValidationError = false;
      }
    });
    this.deliveryElements.betweenFrom.valueChanges.pipe(takeUntil(this.destroyed)).subscribe((value: any) => {
      if (this.deliveryElements.betweenTo.value && this.deliveryElements.betweenTo.value < value) {
        this.timeValidationError = true;
      } else {
        this.timeValidationError = false;
      }
    });
  }

  public get elements() {
    return this.ngOrderForm.controls;
  }

  public get deliveryElements() {
    return (this.elements.deliveryOptions as FormGroup).controls;
  }

  get products(): FormArray {
    return this.ngOrderForm.get('items') as FormArray;
  }

  newProduct(): FormGroup {
    return this.fb.group({
      itemId: [null, Validators.required],
      quantity: [1, Validators.required],
    });
  }

  addProduct() {
    this.products.push(this.newProduct());
  }

  removeProduct(i: number) {
    this.products.removeAt(i);
  }

  createForm() {
    this.ngOrderForm = this.fb.group({
      customerId: [null, Validators.required('[[[[Customer is required]]]]')],
      items: this.fb.array([]),
      shippingAddressId: [{}, Validators.required('[[[[Shipping address is required]]]]')],
      deliveryOptions: this.fb.group({
        deliveryOption: [''],
        withinHours: [null],
        betweenFrom: new FormControl(null),
        betweenTo: new FormControl(null),
      }),
      scheduledDelivery: new FormControl(null),
      deliverAtOnce: [true],
    });
  }

  cancel() {
    if (this.myForm) {
      this.myForm.resetForm();
      this.clearFormArray(this.products);
    }
    this.shippingAddresses = [];
    this.orderClosed.emit(true);
  }

  assignCustomErrorMessage(key: string, error: { error: string; message: string }) {
    // assign custom error messages to controls
    this.errorMessages.find((e) => e.controlName == key)?.errors.push(error);
  }

  submitOrder(formGroup: FormGroup) {
    this.submitted = true;
    if (formGroup.valid) {
      this.isLoading = true;
      const newOrder: ICreateOrder = {
        CustomerId: formGroup.value.customerId.id,
        CustomerAddressId: formGroup.value.shippingAddressId,
        DeliveryOptions: {
          DeliverAtOnce: formGroup.value.deliverAtOnce,
        },
        Products: formGroup.value.items.map((e: any) => ({
          ProductId: e.itemId,
          Quantity: e.quantity,
        })),
      };

      if (formGroup.value.deliveryOptions.deliveryOption == 'within') {
        newOrder.DeliveryOptions.MaxDeliveryTime = formGroup.value.deliveryOptions.withinHours;
      } else if (formGroup.value.deliveryOptions.deliveryOption == 'between') {
        newOrder.DeliveryOptions.AfterTime = moment(formGroup.value.deliveryOptions.betweenFrom).format('H:00');
        newOrder.DeliveryOptions.BeforeTime = moment(formGroup.value.deliveryOptions.betweenTo).format('H:00');
      }

      if (formGroup.value.scheduledDelivery) {
        newOrder.DeliveryOptions.AvailableDeliveryDate = formatDate(
          formGroup.value.scheduledDelivery,
          'MM/dd/yyyy',
          this.sessionService.locale
        );
      }

      this.apiService
        .createOrder(newOrder)
        .pipe(takeUntil(this.destroyed))
        .subscribe((res) => {
          if (res.Result == 'OK') {
            this.orderClosed.emit(true);
            if (this.myForm) {
              this.myForm.resetForm();
              this.clearFormArray(this.products);
            }
            this.orderCreated.emit(true);
            this.alertService.showAlert('success', '[[[[Order Created]]]]!');
          }
          this.isLoading = false;
        });
    }
  }

  onClose(event: boolean) {
    this.shippingAddresses = [];
    this.orderClosed.emit(event);
  }

  clearFormArray = (formArray: FormArray) => {
    while (formArray.length !== 0) {
      formArray.removeAt(0);
    }
    this.addProduct();
  };

  // eslint-disable-next-line no-unused-vars
  appendNewCustomer(_: ICustomer) {
    this.apiService
      .getCustomer(this.customerName)
      .pipe(takeUntil(this.destroyed))
      .subscribe((res) => {
        if (res.Result == 'OK') {
          this.customerList = res.Customers.map(({ Id, Name, Addresses }) => ({
            id: Id,
            name: Name,
            addresses: Addresses,
          }));
          this.elements.customerId.setValue(this.customerList[this.customerList.length - 1]);
        }
      });
  }

  // eslint-disable-next-line no-unused-vars
  appendNewAddress(_: IAddress) {
    this.apiService
      .getCustomer(this.customerName)
      .pipe(takeUntil(this.destroyed))
      .subscribe((res) => {
        if (res.Result == 'OK') {
          // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
          const customer = res.Customers.find((e) => e.Id == parseInt(this.ngOrderForm.value.customerId.id))!;
          this.shippingAddresses = customer.Addresses.map((e) => ({
            id: e.Id,
            name: e.Address,
          }));
          this.elements.shippingAddressId.setValue(this.shippingAddresses[this.shippingAddresses.length - 1].id);
        }
      });
  }

  // eslint-disable-next-line no-unused-vars
  customerClosed(_: boolean) {
    this.addCustomerDisplay = false;
  }

  // eslint-disable-next-line no-unused-vars
  addressClosed(_: boolean) {
    this.addAddressDisplay = false;
  }
}
